@use "../base" as *;
@use "breakpoints" as *;

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: $font-name;
  line-height: 1.4;

  img {
    max-width: 100%;
  }
}
